.App{
  background-color: white;
  padding: none;
  border: none;
}
header{
  font-family: "Monserrat", sans-serif;
  /* padding-bottom: 4%; */
}
nav{
  display:flex;
  justify-content:space-around;
  align-items: center;
  min-height: 8vh;
  background-color: white;
  width: 130%;
  position: fixed;
  z-index: 10;
  
}
.navlinks{
  display:flex;
  justify-content: space-around;
  width:28%;
  
}
.navlinks a{
  text-decoration: none;
  letter-spacing: 3px;
  font-weight:bold;
  font-size:14px;
  color:black;
  
}
.navlinks li{
  list-style: none;
  transition: .5s;
}
.navlinks li:hover{
  background: whitesmoke;
}
.logo{
  text-transform:uppercase;
  letter-spacing: 5px;
  font-size:20px;
}
.logo a{
  text-decoration: none;
  color:black;
}
.btn{
  font-size: 21px;
  padding: 5px 20px;
  border: 0;
  background-color: #3498db;
  color: #fff;
  border-radius:3px;
  transition: all 250ms ease-in-out;
  cursor: pointer;

}
.btn:hover{
  background-color: #2386c8;
}
.btn:focus{
  outline: none;
}
.sidenav {
    height: 100%;
    width: 20%;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #E5E5E5;
    overflow-x: hidden;
    padding-top: 200px;
    list-style-type: none;
  }

  .calender {
    background-color: #E5E5E5;
  }
  
  .sidenav a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
  }
  
  .sidenav a:hover {
    color: black;
  }

  
.basecontainer{

    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    }
    .header{
        font-size: 24px;
        font-family: "Montserrat", sans-serif;
    }
    .content{
        display:flex;
        flex-direction: column;
    }
    .form{
        margin-top: 2em;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .formgroup{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
    label{
        font-size: 20px;
    }
    input{
        margin-top: 6px;
        min-width:18em;
        height: 37px;
        padding: 0px 10px;
        font-size: 16px;
        font-family: "Montserrat", sans-serif;
        background-color:#f3f3f3;
        border: 0;
        border-radius: 4px;
        margin-bottom: 31px;
        transition: all 250ms ease-in-out;
    }
    :focus{
        outline:none;
        box-shadow: 0px 0px 12px .8px #0e81ce96;

    }
    .footer{
        margin-top: 3em;
         
    }
